#pet-room {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 40rem; }
  #pet-room .pet-room-background {
    position: relative;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    box-shadow: 8rem 8rem 12rem var(--contentBgColor) inset, -8rem -8rem 12rem var(--contentBgColor) inset;
    transition: box-shadow 0.5s ease-in-out; }
  #pet-room .mini-small {
    position: absolute;
    z-index: 9;
    top: 25rem;
    left: 50%;
    margin-left: 15rem; }
  #pet-room .pet {
    position: absolute;
    z-index: 10;
    top: 5.8rem;
    height: 35rem;
    width: 35rem;
    left: 50%;
    margin-left: -16rem; }

.pet-actions {
  display: flex;
  margin-bottom: 1em;
  padding: 0.5em 0.8rem;
  justify-content: center; }

.pet-profile-info {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: center; }
  .pet-profile-info table {
    width: 100%; }
  .pet-profile-info h2 {
    display: flex;
    align-items: baseline;
    justify-content: space-between; }

.pet-profile-column {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 0 0.8rem;
  align-items: stretch; }
  .pet-profile-column.pet-profile-column-full {
    width: 100%; }
  @media (max-width: 480px) {
    .pet-profile-column {
      width: 100%; } }

.pet-profile-box {
  width: 100%;
  margin-bottom: 1em;
  padding: 0.5em 1.6rem;
  background: var(--emphasisBgColor);
  border: 1px solid var(--mainShadowColor); }

.pet-favorite-things {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.mini-name {
  font-size: 1.8rem;
  line-height: 200%;
  text-align: center; }
